.App {
  font-family: sans-serif;
  text-align: center;
}

.board {
  margin-left: 32vw;
  margin-right: 35vw;
  display: grid;
  grid-template-columns: repeat(5, 190px);
  grid-template-rows: repeat(5, 190px);
  gap: 2px;
  width: 50vw;
  height: 55vh;
}

.tile {
  background-color: #BFDBAE;
  border: 3px black solid;
  position: relative;
}

.scores {
  display: flex;
  justify-content: space-around;
}

.playerPiece1 {
  border: black 2px solid;
  border-radius: 40px;
  width: 70px;
  height: 70px;
  position: absolute;
  top: 80px;
  left: 6px;
}

.playerPiece2 {
  border: black 2px solid;
  border-radius: 40px;
  width: 70px;
  height: 70px;
  position: absolute;
  top: 80px;
  left: 98px;
}

.playerPic {
  width: 70px;
  height: 70px;
  object-fit: cover;
  border-radius: 40px;
}

.colorCodeBrown{
  border: black solid 1px;
}